h1,
h2 {
  font-family: Lato;
}

#team {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.team-member {
  display: flex;
  height: 350px;
  flex-basis: 25%;
  align-items: flex-start;
  margin: 50px 70px;
}

.spacer2 {
  width: 40%;
  /*min-width: 160px;*/
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: center;
  background-image: linear-gradient(#296965, #fff);
  background-image: linear-gradient(cyan, #ccffff, #fff);
  background-image: linear-gradient(#286a66, #fff);
  background-image: linear-gradient(#000000e3, #ffffff);
  background-image: linear-gradient(#000000e3, #286a66e3, #ffffffe3);
  margin: auto 20px;
  color: #ffffff;
  border-radius: 15px;
  padding: 0 10px;
}

.spacer2 .name {
  font-size: 18px;
  margin-top: 10px;
  line-height: 1.5;
  font-family: 'Lato', Helvetica, Arial, sans-serif;
  font-weight: bold;
}

.spacer2 .title {
  font-size: 16px;
  line-height: 2.5;
  margin-top: 15px;
  font-family: 'Lato', Helvetica, Arial, sans-serif;
  font-style: italic;
}

.bio {
  width: 95%;
  text-align: center;
  margin: 10px 0 0 0;
  font-family: Lato;
  line-height: 1.5;
  color: #000;
  font-family: 'Lato', Helvetica, Arial, sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.fun-facts {
  display: flex;
  font-size: 18px;
  width: 100%;
  height: 50%;
  background: rgba(249, 155, 114, 0.8);
  background: #cc9275;
  color: #fff;
  border-radius: 15px;
  align-items: center;
  justify-content: center;
  line-height: 1.5;
  padding: 0;
  padding-bottom: 10px;
  justify-self: flex-end;
  position: relative;
  font-family: 'Kalam', cursive;
}

.hashtag-funfacts {
  position: absolute;
  bottom: 0px;
  right: 10px;
  /* top: 0px;
  left: 10px; */
  color: #000;
}

.bio .list-item {
  font-size: 15px;
}

.scene {
  perspective: 600px;
}

.card {
  height: 100%;
  cursor: pointer;
  transform-style: preserve-3d;
  transform-origin: center right;
  transition: transform 1s;
  width: 75%;
}

.card.flipped {
  transform: translateX(-100%) rotateY(180deg);
}

.card-face {
  position: absolute;
  text-align: center;
  backface-visibility: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card-front img {
  width: 100%;
  border-radius: 15px;
  align-self: center;
  box-shadow: 0 19px 38px #0000004d, 0 19px 12px #00000038;
}

.card-back {
  transform: rotateY(-180deg);
  background: #cfcfcf !important;
  background: #bbbbbb !important;
  background: #9e9e9e !important;
  background: #8d8d8b !important;
  border-radius: 15px;
  width: 455px;
  min-height: 340px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0 5px 10px #0000004d, 0 5px 3.5px #00000038;
  overflow: hidden;
}

@media (max-width: 1024px) {
  #team {
    flex-direction: column;
    width: 100%;
    margin: 0 auto;
  }

  .team-member {
    width: 800px;
    height: 500px;
    margin: 50px 5%;
    display: flex;
    overflow: visible;
  }

  .team-member:nth-of-type(odd) {
    flex-direction: row;
    align-self: flex-start;
  }

  .team-member:nth-of-type(even) {
    flex-direction: row-reverse;
    align-self: flex-end;
  }

  .spacer2 {
    height: 500px;
  }

  .card-front img {
    width: 100%;
    max-height: 500px;
    display: flex;
  }

  .team-member:nth-child(even) .card-front img {
    align-self: flex-start;
  }

  .card-back {
    width: 800px;
    height: 500px;
  }

  .team-member:nth-of-type(even) .card.flipped {
    transform: translateX(-33%) rotateY(180deg);
  }
}
